// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import VueAxios from 'vue-axios'
import axios from './projects/axios'
import utils from './utils'
import router from './projects/routes.js'
import store from './projects/store'
// IE9+支持,全局babel-polyfill(使用babel-preset-env和useBuiltIns)
import 'babel-polyfill'

import {
  Row, Col
} from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import 'element-ui/lib/theme-chalk/display.css'
Vue.use(Row)
Vue.use(Col)

// 组件中使用 this.$http 调用 axios
Vue.use(VueAxios, axios)

// 组件中使用 this.$utils 调用全局方法
Vue.use(utils)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



