import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

var modules = {}

// 追加当前目录公共存储
const commonfiles = require.context(`./`, true, /\.\/store-\S+\.js$/)

commonfiles.keys().forEach(key => {
  var k = key.replace(/\.\/store-(\S+)\.js$/, '$1')
  modules[k] = commonfiles(key).default
})
// 追加项目目录特有存储
const projectfiles = require.context(`./${process.env.TARGET_PROJECT}/`, true, /\.\/store-\S+\.js$/)

projectfiles.keys().forEach(key => {
  var k = key.replace(/\.\/store-(\S+)\.js$/, '$1')
  modules[k] = projectfiles(key).default
})

export default new Vuex.Store({
  modules: modules,
  strict: process.env.NODE_ENV !== 'production'
})



