export default [
  {
    path: '',
    meta: {
      layoutHeader: r => require.ensure([], () => r(require('./view/MainHeader')), 'main'),
      layoutSide: r => require.ensure([], () => r(require('./view/MainSide')), 'main'),
      layoutFooter: r => require.ensure([], () => r(require('./view/MainFooter')), 'main')
    },
    component: r => require.ensure([], () => r(require('./view/MainContent')), 'main')
  },
  {
    path: '/about',
    meta: {
      layoutHeader: r => require.ensure([], () => r(require('./view/MainHeader')), 'about'),
      layoutSide: r => require.ensure([], () => r(require('./view/AboutSide')), 'about'),
      layoutFooter: r => require.ensure([], () => r(require('./view/MainFooter')), 'about')
    },
    component: r => require.ensure([], () => r(require('./view/AboutContent')), 'about')
  },
  {
    path: '/map',
    meta: {
      layoutHeader: r => require.ensure([], () => r(require('./view/MainHeader')), 'map')
    },
    component: r => require.ensure([], () => r(require('./view/MapContent')), 'map')
  }
]



