import admin from './admin'
import main from './main'
export default [
  {
    path: '/',
    component: r => require.ensure([], () => r(require('@/components/Layout')), 'main'),
    children: main
  }, {
    path: '/admin',
    component: r => require.ensure([], () => r(require('@/components/Layout')), 'admin'),
    children: admin,
    meta: {
      requiresAuth: true
    }
  }
]



